export default {
  onLoad: function(e) {
    this.rowCount = 7;
    this.columnCount = 7;
    this.padding = 50;
    this.radius = 10;
    
    this.isMe = true;
    this.chessBox = [];
    for (let i = 0; i < this.rowCount; i += 1) {
      this.chessBox[i] = []
      for (let j = 0; j < this.columnCount; j += 1) {
        this.chessBox[i][j] = 0;
      }
    }

    this.boardTask = setTimeout(() => {
      this.ctx = uni.createCanvasContext('myCanvas');
      this.designBoard();
      this.ctx.draw();
    }, 100)
  },
  onUnload() {
    clearTimeout(this.boardTask);
  },
  methods: {
    designBoard() {
      for (let i = 1; i <= this.rowCount; i += 1) {
        this.ctx.moveTo(0, this.padding * i);
        this.ctx.lineTo(400, this.padding * i);
      }
      for (let j = 1; j <= this.columnCount; j += 1) {
        this.ctx.moveTo(this.padding * j, 0);
        this.ctx.lineTo(this.padding * j, 400);
      }
      this.ctx.stroke();
    },
    designChess(x, y) {
      if (this.chessBox[x][y] === 0) {
        this.chessBox[x][y] = this.isMe ? 1 : 2;
        this.isMe = !this.isMe;
        this.ctx.beginPath();
        this.ctx.arc((x + 1) * this.padding, (y + 1) * this.padding, this.radius, 0, 2 * Math.PI);
        this.ctx.closePath();
        this.ctx.fillStyle = this.chessBox[x][y] === 1 ? 'black' : 'red';
        this.ctx.fill();
      }
      // for (let xx = 0; xx < this.rowCount; xx += 1) {
      //   for (let yy = 0; yy < this.columnCount; yy += 1) {
      //     if (this.chessBox[xx][yy] === 0)
      //       continue;
      //     this.ctx.beginPath();
      //     this.ctx.arc((xx + 1) * this.padding, (yy + 1) * this.padding, this.radius, 0, 2 * Math.PI);
      //     this.ctx.closePath();
      //     this.ctx.fillStyle = this.chessBox[xx][yy] === 1 ? 'black' : 'red';
      //     this.ctx.fill();
      //   }
      // }
    },
    handleTouchEnd(e) {
      const pos = e.changedTouches[0];
      // this.designBoard();
      const x = Math.floor((pos.x - 2 * this.radius) / this.padding);
      const y = Math.floor((pos.y - 2 * this.radius) / this.padding);
      this.designChess(x, y);
      this.ctx.draw(true);
    },
    // writeMessage(message) {
    //   this.context.clearRect(0, 0, this.context.width, this.context.height);
    //   this.context.font = 'italic 20px "Fira Sans", serif';
    //   this.context.fillText(message, 25, 25);
    //   this.context.draw();
    // },
  }
}
